import React, {useEffect} from "react";
import {Anchor, ConfigProvider, Layout, theme} from 'antd';
import './style.css'
import {CaretDownFilled, CaretRightFilled, FolderFilled, FolderOpenFilled} from "@ant-design/icons";
const { Sider } = Layout;

export const MySider = ({items, setItems}) => {
    // const {
    //     token: { colorBgContainer },
    // } = theme.useToken();

    useEffect(() => {
        // const antAnchorLinkDOM = document.getElementsByClassName("ant-anchor-link")
        // const arrowRightLogo = <CaretRightFilled />
        // const folderFilledLogo = <FolderFilled />
        // console.log(antAnchorLinkDOM)


    }, []);

    return(
        <ConfigProvider
            theme={{
                token: {
                    lineType: 'none',
                    lineWidthBold: 0,
                }
            }}
        >
            <Sider
                style={{
                    // background: colorBgContainer,
                    padding: '10px'
                }}
                width="15rem"
                // width="250px"
                collapsedWidth="0"
                // collapsed={false}
                breakpoint='xl'
            >
                <div className="Container">
                    <div className="Folder">
                        Folders
                    </div>
                    <div className="Components">
                        <div className="ArrowDownLogo">
                            <CaretDownFilled />
                        </div>
                        <div className="FolderOpenLogo">
                            <FolderOpenFilled/>
                        </div>
                        Components
                    </div>
                </div>
                <Anchor
                    getContainer={() => (document.getElementById("ContentContainer"))}
                    items={items}
                    // getCurrentAnchor={activeLink => {
                    //     console.log(activeLink)
                    // }}
                    onClick={(e, link) => {
                        if(link.href === '#CloseAll'){
                            const temp = items.map(i => {
                                i.open = 'false'
                                return i
                            })
                            setItems(temp)
                            e.preventDefault()
                            return
                        }

                        const temp = items.find(i => i.title === link.title)
                        const temp2 = items.map(i => {
                            if(i === temp){
                                temp.open = 'true'
                                return temp
                            }
                            else{
                                return i
                            }
                        })

                        // console.log(temp2)
                        setItems(temp2)
                        e.preventDefault()
                        // console.log(temp2)
                    }}
                />
            </Sider>
        </ConfigProvider>
    )
}