import {Image} from "antd";
import './style.css'
export const EducationContainer = ({image, text}) => {
    return(
        <div className="EducationContainer">
            <div className="ImageContainer">
                <Image
                    src={image}
                    preview={{
                        maskClassName: "MaskContainer"
                    }}/>
            </div>

            <div className="TextContainer">
                <div className="MyText">
                    {text}
                </div>
            </div>

        </div>
    )
}