import {TypeAnimation} from "react-type-animation";
export const MHT = () => {
    return(
        <>
            <h2>
                <TypeAnimation
                    sequence={[
                        100,
                        'SMAN Unggulan M.H. Thamrin',
                    ]}
                    wrapper="i"
                    speed={{type: "keyStrokeDelayInMs", value: 40}}
                    cursor={false}
                    // style={{ fontSize: '2em', display: 'inline-block' }}
                    // repeat={Infinity}
                />
            </h2>

            <TypeAnimation
                sequence={[
                    100,
                    'Natural Science Stream (2019-2021)',
                ]}
                wrapper="h5"
                speed={{type: "keyStrokeDelayInMs", value: 20}}
                cursor={false}
                // style={{ fontSize: '2em', display: 'inline-block' }}
                // repeat={Infinity}
            />

            {/*<Title style={{*/}
            {/*    margin: '0 0 0.5rem 0',*/}
            {/*}}>Relevant courses:</Title>*/}
            {/*<Paragraph> Object-Oriented Programming, Data Structures and Algorithm, Intro to Data Science & Artificial Intelligence, Software Engineering, Computer Communication, Operating Systems, Intro to Design & Project, Design & Innovation Project</Paragraph>*/}

        </>
    )
}