import React, {useEffect, useState} from "react";
import {Collapse, Layout} from "antd";
import './style.css'
import {MyDivText} from "./DivComponent/MyDivText";
import {CaretLeftFilled, CaretRightFilled, CloseOutlined} from "@ant-design/icons";
import {DivContentContainer} from "./DivComponent/DivContentContainer";


const { Content} = Layout;
export const MyContent = ({items, setItems, hasScrolledToAboutMe}) => {
    // const {
    //     token: { colorBgContainer },
    // } = theme.useToken();

    const [myActiveKey, setMyActiveKey] = useState([])

    useEffect(() => {
        setMyActiveKey(items.reduce((result, currentItem) => {
            if(currentItem.open === 'true'){
                result.push(currentItem.key)
            }
            return result
        }, []))
        // console.log(items[0].ref)
    }, [items]);

    return(
        <Content
            style={{
                // padding: '0 24px',
                minHeight: 280,
            }}
        >
            <div className="ContentContainer" id={'ContentContainer'}>
                <div className="TabsContainer">
                    <div className="Symbols">
                        <CaretLeftFilled style={{
                            // fontSize:'20px'
                        }}/>
                        <CaretRightFilled />
                    </div>
                    <div className="Tab1">
                        <span>Project</span>
                        <div className="CloseSymbol">
                            <CloseOutlined style={{
                                // fontSize: '20px'
                            }}/>
                        </div>
                    </div>
                </div>
                <div className="Content">
                    <div className="TopBoilerplate">
                        <div>
                            {`<!DOCTYPE html>`}
                        </div>
                        <div>
                            {`<html lang="en">`}
                        </div>
                        <div>
                            &emsp;&emsp;{`<head>`}
                        </div>
                        <div>
                            &emsp;&emsp;&emsp;&emsp;{`<meta charset="utf-8" />`}
                        </div>
                        <div>
                            &emsp;&emsp;&emsp;&emsp;{`<link rel="icon" href="%PUBLIC_URL%/favicon.ico" />`}
                        </div>
                        <div>
                            &emsp;&emsp;&emsp;&emsp;{`<meta`}
                        </div>
                        <div>
                            <span className="">&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;{`name`}</span>
                            <span className="">{`=description`}</span>
                        </div>
                        <div>
                            <span className="">&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;{`content=`}</span>
                            <span className="TextGrey">{`Welcome to my personal page`}</span>
                        </div>
                        <div>
                            &emsp;&emsp;&emsp;&emsp;{`/>`}
                        </div>


                        <div>
                            &emsp;&emsp;{`<title>Personal Page</title>`}
                        </div>
                        <div>
                            &emsp;&emsp;{`</head>`}
                        </div>
                        <div>
                            &emsp;&emsp;{`<body>`}
                        </div>

                    </div>
                    {items.map((i) => {
                        if(i.href !== '#CloseAll'){
                            return(
                                <div id={i.key}>
                                    <Collapse
                                        key={i.key}
                                        // collapsible="header"
                                        activeKey={myActiveKey}
                                        destroyInactivePanel = {true}
                                        onChange={e => {
                                            setMyActiveKey(e)

                                            const temp = items.find(k => i.key === k.key)
                                            const temp2 = items.map(k => {
                                                if(k === temp){
                                                    if(temp.open === 'false'){
                                                        temp.open = 'true'
                                                    }
                                                    else {
                                                        temp.open = 'false'
                                                    }
                                                    return temp
                                                }
                                                else{
                                                    return k
                                                }
                                            })
                                            setItems(temp2)
                                        }}
                                        // defaultActiveKey={['part-1']}
                                        items={[
                                            {
                                                key: i.key,
                                                showArrow: false,
                                                label: <MyDivText title={i.titleText} open={i.open} ref={i.ref}/>,
                                                // children: <MyDivComponent id={i.id} style={styles[j]} title={i.title}/>,
                                                children: <DivContentContainer element={i.element} open={i.open}/>,
                                                forceRender: (i.key === 'AboutMe' ? !hasScrolledToAboutMe : false)
                                            }
                                        ]}
                                    />
                                </div>
                            )
                        }
                    })}
                    <div className="BottomBoilerPlate">
                        <div>
                            &emsp;&emsp;{`</body>`}
                        </div>
                        <div>
                            {`</html>`}
                        </div>
                    </div>
                </div>
            </div>
        </Content>
    )
}
