import {TypeAnimation} from "react-type-animation";


export const NTU = () => {
    return(
        <>
            <h2>
                <TypeAnimation
                    sequence={[
                        100,
                        'Nanyang Technological University',
                    ]}
                    wrapper="i"
                    speed={{type: "keyStrokeDelayInMs", value: 40}}
                    cursor={false}
                    // style={{ fontSize: '2em', display: 'inline-block' }}
                    // repeat={Infinity}
                />
            </h2>

            <TypeAnimation
                sequence={[
                    100,
                    'Bachelor of Information Engineering and Media (2021-2025), ASEAN Scholarship',
                ]}
                wrapper="h5"
                speed={{type: "keyStrokeDelayInMs", value: 20}}
                cursor={false}
                style={{whiteSpace: 'pre-line'}}
                // style={{ fontSize: '2em', display: 'inline-block' }}
                // repeat={Infinity}
            />
            <br/>

            <TypeAnimation
                sequence={[
                    100,
                    'Relevant courses:',
                ]}
                wrapper="h3"
                cursor={false}
                speed={{type: "keyStrokeDelayInMs", value: 40}}
                // style={{ fontSize: '2em', display: 'inline-block' }}
                // repeat={Infinity}
            />

            <TypeAnimation
                sequence={[
                    'Object-Oriented Programming, Data Structures and Algorithm, Intro to Data Science & Artificial Intelligence, Software Engineering, Computer Communication, Intro to Design & Project, Design & Innovation Project',
                ]}
                wrapper="p"
                cursor={false}
                speed={{type: "keyStrokeDelayInMs", value: 1}}
                // style={{ fontSize: '2em', display: 'inline-block' }}
                // repeat={Infinity}
            />

            {/*<Title style={{*/}
            {/*    margin: '0 0 0.5rem 0',*/}
            {/*}}>Relevant courses:</Title>*/}
            {/*<Paragraph> Object-Oriented Programming, Data Structures and Algorithm, Intro to Data Science & Artificial Intelligence, Software Engineering, Computer Communication, Operating Systems, Intro to Design & Project, Design & Innovation Project</Paragraph>*/}

        </>
    )
}