import {Button, ConfigProvider, Modal} from "antd";
import React, {useState} from "react";
import { Image } from 'antd';
import './style.css'
import {TypeAnimation} from "react-type-animation";
export const MyModal = ({img, video, title, description, maskTitle = title, maskDescription, resources}) => {
    const [isModalOpen, setIsModalOpen] = useState(false);
    const showModal = () => {
        setIsModalOpen(true);
    };
    const handleOk = () => {
        setIsModalOpen(false);
    };
    const handleCancel = () => {
        setIsModalOpen(false);
    };

    return(
        <div className="MyModal">
            <div className="MyModalButtonContainer">
                <Button type="primary" onClick={showModal} >
                    <Image
                        src={img}

                        preview={{
                            mask: (
                                <>
                                    <div className={"title"}>
                                        <h3>{maskTitle}</h3>
                                    </div>
                                    <div className={"description"}>
                                        <p>{maskDescription}</p>
                                    </div>
                                </>
                            ),
                            maskClassName: "ModalMaskContainer",
                            visible: false
                        }}
                    />
                </Button>
            </div>


            <Modal
                // width={'500px'}
                title={
                    (video ?
                            <Image
                                src={img}
                                // width='200px'
                                // height='400px'
                                preview={{
                                    imageRender: () => (
                                        <video
                                            muted
                                            width="80%"
                                            controls
                                            src={video}
                                            autoPlay={true}
                                        />
                                    ),
                                    toolbarRender: () => null,
                                }}

                            /> :
                            <Image
                                src={img}
                                // width='100%'
                                // height={'300px'}
                            />
                    )
                }
                open={isModalOpen}
                onOk={handleOk}
                onCancel={handleCancel}
                closeIcon={false}
                // destroyOnClose={true}
                footer={null}
            >
                <TypeAnimation
                    sequence={[
                        100,
                        title,
                    ]}
                    wrapper="h2"
                    speed={{type: "keyStrokeDelayInMs", value: 20}}
                    cursor={false}
                />
                <TypeAnimation
                    style={{whiteSpace: 'pre-line'}}
                    sequence={[
                        100,
                        description,
                    ]}
                    wrapper="p"
                    speed={{type: "keyStrokeDelayInMs", value: 5}}
                    cursor={false}
                />
                <ConfigProvider
                    theme={{
                        token: {
                            colorBgContainer: 'rgba(102,102,102,0.63)',
                            colorLinkActive: 'lightgrey',
                            colorPrimary: 'lightgrey',
                            colorPrimaryHover: 'lightgrey',
                            // defaultBorderColor: 'black'
                        },
                        components: {
                            Button: {
                                defaultBorderColor: 'rgba(102,102,102,0.63)'
                                // defaultShadow: 'red',
                                // paddingContentHorizontal: '20px'
                            }
                        }
                    }}
                >
                    <div className="InsideModalButtonContainer">
                        {resources ? resources.map(resource => (

                                <Button onClick={() => {
                                    window.open(resource.link, '_blank', 'noopener,noreferrer')
                                }}>{resource.title}</Button>
                        )) :
                            <></>
                        }
                    </div>
                </ConfigProvider>
                {/*<div className="ButtonInsideModal">*/}
                {/*    <Button>Primary Button</Button>*/}
                {/*</div>*/}
            </Modal>
        </div>
    )
}