import {cloneElement, forwardRef} from "react";
import './style.css'

export const DivContentContainer = ({element, open}) => {
    const clonedElement = cloneElement(
        element,
        {open: open}
    )
    // console.log(open)
    return(
        <div className="DivContentContainer">
            {clonedElement}
        </div>
    )
}

