import {Layout, theme} from "antd";
import {MySider} from "./Sider";
import React, {useCallback, useEffect, useRef, useState} from "react";
import {MyContent} from "./Content";
import './style.css'
import {AboutMe} from "./Content/DivComponent/AboutMe";
import {Education} from "./Content/DivComponent/Education";
import {RecentProjects} from "./Content/DivComponent/RecentProjects";
import {LogoContainer} from "./Sider/LogoContainer";
import useIsCollapseContentOpen from "../Utils/Hooks/useIsCollapseContentOpen";

const { Content } = Layout;

export const MyBody = () => {
    // RefList ---------------------------------------------------------------------------------------------------------
    const AboutMeRef = useRef(null)
    const EducationRef = useRef(null)
    const RecentProjectsRef = useRef(null)

    // ItemSet---------------------------------------------------------------------------------------------------------
    const [items, setItems] = useState([
        {
            key: 'AboutMe',
            href: '#AboutMe',
            title: (
                <LogoContainer text="About Me"/>
            ),
            open: 'false',
            element: <AboutMe/>,
            titleText: 'About Me',
            ref: AboutMeRef
        },
        {
            key: 'Education',
            href: '#Education',
            title: (
                <LogoContainer text="Education"/>
            ),
            open: 'false',
            element: <Education/>,
            titleText: 'Education',
            ref: EducationRef
        },
        {
            key: 'RecentProjects',
            href: '#RecentProjects',
            title: (
                <LogoContainer text="Recent Projects"/>
            ),
            open: 'false',
            element: <RecentProjects/>,
            titleText: 'Recent Projects',
            ref: RecentProjectsRef
        },
        {
            key: 'CloseAll',
            href: '#CloseAll',
            title: (
                <LogoContainer text="Close All"/>
            ),
        },
    ])
    
    const [firstCollapseItemNode, setFirstCollapseItemNode] = useState(null)

    // antd Tokens------------------------------------------------------------------------------------------------------
    const {
        token: { colorBgContainer },
    } = theme.useToken();
    

    // Code to check if first collapse is open--------------------------------------------------------------------------
    useEffect(() => {
        let temp = items[0].ref.current.parentNode.parentNode.parentNode
        setFirstCollapseItemNode(temp)
    }, []);

    const [firstCollapseContentOpen, observer] = useIsCollapseContentOpen(firstCollapseItemNode)
    // Scroll to first collapse-----------------------------------------------------------------------------------------

    const [hasScrolledToAboutMe, setHasScrolledToAboutMe] = useState(false);
    useEffect(() => {
        if(!hasScrolledToAboutMe){
            const tempItems = [...items]
            tempItems[0].open = 'true'
            setItems(tempItems)
            // console.log(firstCollapseContentOpen)

            if(firstCollapseContentOpen){
                AboutMeRef.current.scrollIntoView({
                    behavior: 'smooth',
                    block: 'start',
                })
                setHasScrolledToAboutMe(true)
                observer.disconnect()
            }
        }
    }, [firstCollapseContentOpen]);


    return(
        <Content
            style={{
                // padding: '0 50px',
            }}
        >
            <Layout
                style={{
                    // padding: '24px 0',
                    background: colorBgContainer,
                    height: '100%'
                }}
            >

                <MySider items={items} setItems={setItems}/>
                <MyContent items={items} setItems={setItems} hasScrolledToAboutMe={hasScrolledToAboutMe}/>

            </Layout>
        </Content>
    )
}