import './style.css'
import {MyModal} from "./Components/MyModal";
import SmackVideo from '../../../../Assets/Videos/Smack.mp4'
import SmackImage from '../../../../Assets/Images/Smack.png'
import SudokuImage from '../../../../Assets/Images/Sudoku.png'
import GTDWebsiteImage from '../../../../Assets/Images/GTD XXIV Website.png'
import GTDWebsideVideo from '../../../../Assets/Videos/GTD XXIV Website (2021).mp4'
import {TypeAnimation} from "react-type-animation";
import QuizzyImage from '../../../../Assets/Images/Quizzy.png'
import PintuAppImage from '../../../../Assets/Images/PintuApp.png'


export const RecentProjects = () => {

    return(
        <div className="RecentProjects">
            <TypeAnimation
                sequence={[
                    350,
                    'Recent Projects',
                ]}
                wrapper="h1"
                speed={40}
                // style={{ fontSize: '2em', display: 'inline-block' }}
                // repeat={Infinity}
            />
            <div className="ModalContainer">
                <MyModal
                    img={PintuAppImage}
                    title={"PINTU App"}
                    description={"A React-based website created for PINTU, the Indonesian students association at NTU. It serves as a platform to connect Indonesian students within NTU. \n\n My primary focus was on developing the front-end of the Marketplace, enabling users to buy and sell items."}
                    maskDescription={"Organization Website"}
                    resources={[
                        {
                            link: 'https://pintusingapura.org/',
                            title: 'General Website'
                        },
                        {
                            link: 'https://app.pintusingapura.org/',
                            title: 'PINTU App Link'
                        },
                    ]}
                />
                <MyModal
                    img={QuizzyImage}
                    title={"Quizzy"}
                    description={"An Android app developed as a project for the NTU module IM2073. The app, created using Java, serves to function as a quiz platform where students can view questions on a computer screen and submit their answers via their phones. \n\n I handled the backend components, which included the database, server, and API. I also handled the integration between the front end and back end. The server uses Apache Tomcat, as per the professor's request, while my partner takes care of remaining aspects of the project."}
                    maskDescription={"Android App"}
                    resources={[
                        {
                            link: 'https://drive.google.com/file/d/1ySLvrJG8y5eV5cm9IClX7lUEn7J6cLI4/view?usp=sharing',
                            title: 'Documentation'
                        },
                        {
                            link: 'https://github.com/gabriellenicole/quizzy-mobile',
                            title: 'Git Project'
                        },
                    ]}
                />
                <MyModal
                    img={SmackImage}
                    video={SmackVideo}
                    title={"Smack"}
                    description={"A web project undertaken for the NTU module IM2073. Built using React, my partner and I collaborated to create a simple e-commerce website for shopping.\n\n My primary focus was on the backend functionality, encompassing the database, APIs, server, and more. The backend uses Apache Tomcat, as per the module requirements."}
                    maskDescription={"E-commerce Website"}
                    resources={[
                        {
                            link: 'https://drive.google.com/file/d/1jbdlcYBkAp7LVd7OQaqmFVmne-EC8nzM/view?usp=sharing',
                            title: 'Report'
                        },
                        {
                            link: 'https://github.com/aftanza/IM2073-smack-backend',
                            title: 'Backend'
                        },
                        {
                            link: 'https://github.com/gabriellenicole/smack-ecommerce',
                            title: 'Frontend'
                        }
                    ]}
                />
                <MyModal
                    img={GTDWebsiteImage}
                    video={GTDWebsideVideo}
                    title={"GTD Website"}
                    description={"A React-based website for the Get Together Day (GTD) organization, a bonding initiative created by and for Indonesian students at NTU.\n\n As a part of a collaborative team of ten, my primary focus was the development of various front-end components, including the Footer, the FAQ page, and more."}
                    maskDescription={"Organization Website"}
                    resources={[
                        {
                            link: 'https://web.archive.org/web/20220409021650/https://pintugtd.com/https://web.archive.org/web/20220409021650/https://pintugtd.com/',
                            title: 'Web Archive'
                        },
                        {
                            link: 'https://github.com/GTD-IT-XXIV/gtd-xxiv-website',
                            title: 'Git Project'
                        },
                    ]}
                />
                <MyModal
                    img={SudokuImage}
                    title={"Sudoku"}
                    description={"My partner and I created our version of the Sudoku game for the NTU module IM1003. \n\n My primary contributions were in developing the game's logic and implementing multiplayer functionality (LAN only)."}
                    maskDescription={"Java App"}
                    resources={[
                        {
                            link: 'https://github.com/aftanza/Sudoku',
                            title: 'Sudoku Project'
                        }
                    ]}
                />
            </div>
            {/*<MyModal></MyModal>*/}

        </div>
    )
}