import {Layout} from "antd";
import {SwitcherTwoTone} from "@ant-design/icons";
import './style.css'


const { Footer } = Layout;

export const MyFooter = () => {
    return(
        <Footer
            style={{
                // textAlign: 'center',
            }}
        >
            <div className="FooterContainer">
                <div className="Symbol">
                    <SwitcherTwoTone twoToneColor='#7B8584'/>
                </div>
                <div className="Empty"></div>
                <div className="Space">
                    4 Spaces
                </div>
                <div className="Type">
                    HTML
                </div>
            </div>
        </Footer>

    )
}