export const MyDivText = forwardRef(({title, open}, ref) => {
    return(
        <div className="MyDivText" ref={ref}>
            <span>&emsp;&emsp;&emsp;&emsp;</span>
            <span>{`<div id=`}</span>
            <span className="TextGrey">{title} </span>
            <span>{open === 'true' ? "class=\"ant-collapse-item ant-collapse-item-active\"" : "class=\"ant-collapse-item\""}</span>
            <span>{`>`}</span>
            <span>{'</div>'}</span>
        </div>
    )
})
