import {useCallback, useEffect, useState} from "react";
import useMutationObservable from "../useMutationObservable";

export default function useIsCollapseContentOpen(collapseItemNode){
    const [collapseContentDOM, setCollapseContentDOM] = useState(null)
    const [collapseContentOpen, setCollapseContentOpen] = useState(false)
    const [collapseContentHeight, setCollapseContentHeight] = useState('')
    const [collapseContentDOMUpdate, setCollapseContentDOMUpdate] = useState(true)

    const onListMutation = useCallback(
        (mutationList) => {

            for (const mutation of mutationList) {
                // console.log(mutation)
                if (mutation.type === "attributes") {
                    setCollapseContentDOMUpdate(!collapseContentDOMUpdate)
                    // console.log(`The ${mutation.attributeName} attribute was modified.`);
                }
            }
        },
        [collapseContentDOMUpdate, setCollapseContentDOMUpdate]
    );

    useEffect(() => {
        if(!collapseItemNode) return
        // let collapseItemNode = items[0].ref.current.parentNode.parentNode.parentNode
        let collapseContent = collapseItemNode.children[1]
        setCollapseContentDOM(collapseContent)
        // console.log(collapseContent)
    }, [collapseItemNode]);

    const options = {
        config: { attributes: true, childList: false, subtree: false }
    }
    const observer = useMutationObservable(collapseContentDOM, onListMutation, options);

    useEffect(() => {
        if(!collapseContentDOM) return
        let currentHeight = collapseContentDOM.style.height

        setCollapseContentHeight(currentHeight)
        // console.log(currentHeight)

        if(currentHeight !== collapseContentHeight){
            if(currentHeight === ''){
                if(collapseContentHeight !== '0px'){
                    setCollapseContentOpen(true)
                }else {
                    setCollapseContentOpen(false)
                }
            }
        }
        // console.log(firstCollapseContentOpen)
        // console.log(currentHeight)
    }, [collapseContentDOM, collapseContentDOMUpdate]);

    return [collapseContentOpen, observer]
}