export const Education = () => {
    return(
        <div className="Education">
            <TypeAnimation
                sequence={[
                    350,
                    'Education',
                ]}
                wrapper="h1"
                speed={40}
                // style={{ fontSize: '2em', display: 'inline-block' }}
                // repeat={Infinity}
            />
            <EducationContainer image={LogoNTU} text={<NTU/>}/>
            <EducationContainer image={LogoMHT} text={<MHT/>}/>
        </div>
    )
}